:root{
    --red-color: #FF4F6E;
    --blue-color:#009DAE ;
    --yellow-color: #FFAF00;
}


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Montserrat;

}
body{
    background-color: #fff;
    overflow-x: hidden;
    
}

p.title{
    font-family: GillSansUltraBold;
    pointer-events: none;
    cursor: default;

}

img.absolute{
    position: absolute;
}

img.absolute.red{
    top: -1000px;
}

/* img.absolute.blue{
    ;
} */

.banner{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 30px;
    position: relative;
    padding-bottom: 0;
}

.banner img.absolute.red{
    top: -150%;
}

.banner .image img{
    height: 35em;
    margin-right: 6em;
}

 p.subtitle{
    color:var(--red-color);
    font-weight: bold;
    font-size: 1.8em;
    margin-bottom: 10px;
}


p.title{
    font-weight: bold;
    font-size: 3.5em;
}

.banner .content button{
    padding: 20px;
    border: none;
    margin-top: 40px;
    position: relative;
    padding-left: 70px;
    background-color: transparent;
    outline: none;
    z-index: 1;
    overflow: hidden;
    border-top-right-radius: 30px;
    cursor: pointer;
}

.banner .content button span{
    position: relative;
    z-index: 3; 
    color: black; 
    font-weight: bold;
    font-size: 1.1rem;
}

.banner .content button::after{
    content: "";
    background-color: var(--red-color);
    color: #FF4F6E;
    position: absolute;
    height: 4em;
    width: 4.3em;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    border-top-right-radius: 10px;
    background-image: url(../assets/images/arrow_tr.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 40%;
    z-index: 2;
}

.banner .content button:hover::after {
    cursor: pointer;
    width: 200px;
    background-size: 25px;
    border-top-right-radius: 30px;
    background-position: 20px 50%;
}

.banner .content button:hover span{
    color: #fff;
}

.banner .content span.line{
    height: 2px;
    width: 100%;
    background-color:rgb(230, 230, 230);
    display: block;
    margin-top: 40px;
}

.socials{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.socials img{
    margin: 0 30px;
    height: 4em;
    margin-top: 0;
}


.services{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 40px 70px;
    margin: 70px 0;
    position: relative;
}

.services img.absolute.blue{
    top: -200%;
    right: -80%;
}


.services .left{
    width: 30%;
}

.services .left p.title{
    font-size: 2em;
}


.services .right {
    position: relative; 
    z-index: 1; 
    display: flex;
}

.services .right .block, .projects .bottom .block {
    position: relative; 
}

.services .right .block img.abs,.projects .bottom .block img.abs {
    position: absolute;
    z-index: 1; 
    
}

.services .right .block:first-child img.abs{
    bottom: -17%;
    left: -10%; 
}


.services .right .block:last-child img.abs{
    top: -17%;
    right: -10%;
}

.services .right .service {
    position: relative; /* Ensure z-index applies to service div */
    z-index: 5; /* Higher z-index for service to appear above bubbles */
    box-shadow: 1px 0px 10px rgba(0, 0, 0, .2);
    padding: 20px 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 200px;
    min-height: 300px;
    width: 300px;
    margin: 0 20px;
    border-radius: 11px;
    background-color: white; /* Add a background to visually hide the bubbles */
}

.services .right .service p {
    font-weight: 600;
    font-size: 1.2em;
    max-width: 70%;
    text-align: center;
    margin-bottom: 30px;
}

.about{
    padding: 70px;
}
.about .header{
    margin-bottom: 50px;
}
.about .header p.title{
    font-size: 2em;
}


.about .content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.about .content .right{
    display: flex;
    flex-direction: column;
}
.about .content .right .top{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;
}

.about .content .right .top .service{
    box-shadow: 1px 0px 10px rgba(0, 0, 0, .2);
    padding: 20px 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 200px;
    min-height: 160px;
    /* height: 200px; */
    margin-right: 20px;
    border-radius: 11px;
    background-color: white
}
.about .content .right .top .service img{
    height: 38px;
    width: 38px;
}

.about .content .right .top .service p{
    font-weight: 700;
}

.about .content .right .top .service p.desc{
    font-weight: 600;
    opacity: .6;
    text-align: center;
    margin-top: 10px;
}

.about .content .right .desc p{
    font-weight: 700;
}

.projects{
    padding: 0 70px 70px 70px;
}

.projects .bottom{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 0;
}

.projects .header{
    width: 100%;
    text-align: center;
    margin-bottom: 70px;
}
.projects .header p.title{
    font-size: 2em;
}


.projects .bottom .service {
    position: relative; /* Ensure z-index applies to service div */
    z-index: 5; /* Higher z-index for service to appear above bubbles */
    box-shadow: 1px 0px 10px rgba(0, 0, 0, .2);
    padding: 20px 30px;
    display: flex;
    text-align: center;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    min-width: 200px;
    min-height: 300px;
    width: 300px;
    margin: 0 20px;
    border-radius: 11px;
    background-color: white; /* Add a background to visually hide the bubbles */
}

.projects .bottom .service .subtitle{
    color: rgba(0, 0, 0, 0.521);
    font-size: 1em;
}

.projects .bottom .service a{
    text-decoration: none;
    color: black;
}

.projects .bottom .service p{
    font-weight: 700;
    font-size: 1.4em;
}

.projects .bottom .service img{
    height: 70px;
    width: 50%;
}

.projects .bottom .service .buttons{
    margin-top: 30px;
    display: flex;

}

.projects .bottom .service a.exp{
    background-color: #FFAF00;
    padding: 10px 20px;
    border-radius: 5px;
    color: #fff;
    display: flex;
    align-items: center;
}


.projects .bottom .service .buttons button{
    padding: 10px 20px;
    background-color: transparent;
    outline: none;
    border: none;
    color: #FFAF00;
    font-weight: 700;
    background-image: url(../assets/images/arrow_down.png);
    background-size: 10%;
    background-repeat: no-repeat;
    background-position: right;
}

.projects .bottom .service .info{
    margin-top: 20px;
    text-align: left;
    transform-origin: top;
    width: 85%;
    transition: display ease 1.5s;
}

.projects .bottom .service .info.hidden{
    display: none;
}

.projects .bottom .service .info ul li{
    margin: 5px 0;
}

.projects .bottom .service img.bar{
    width: 30%;
    background-color: #000;
    padding: 10px;
    border-radius: 10px;
}

.projects .bottom .block{
    position: relative;
}

.projects .bottom .block img.abs.first{
    bottom: -17%;
    left: -10%;
}

.projects .bottom .block img.abs.last {
    top: -17%;
    right: -10%;
}

.projects .bottom .block.triangles::after{
    content: "";
    position: absolute;
    z-index: 10;
    height: 60px;
    width: 60px;
    top: 0;
    right: -5%;
    background: url(../assets/images/triangles.png);
    background-size: contain;
    background-repeat: no-repeat;
}

.projects{
    position: relative;
    z-index: 2;
}

.projects img.absolute{
    left: -20%;
    top: -40%;
    z-index: 1;
}

.projects img.absolute.circles{
    left: 5%;
    top: 10%;
}

.projects img.absolute.purple{
    left: 22%;
    top:-5%;
}

.projects img.absolute.open{
    left: 15%;
    top: 25%;
}


.skills{
    margin-top: 100px;
}

.skills .bottom .list {
    list-style: none;
    margin: 0 40px;
}

.skills .bottom .list li{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
}

.skills .bottom .list li p{
    margin-right: 15px;
    font-weight: 700;
    font-size: 1.3em;
}

.skills .bottom .list li img{
    scale: .8;
}


.contact{
    min-height: 50vh;
    background-color: #009DAE;
    background: linear-gradient(#fff, #009dae);
    margin-top: 300px;
}

.contact form{
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 40%;
    background-color: #fff;
    padding: 40px 80px;
    /* border: 3px solid #009DAE; */
    box-shadow: 0px 5px 10px  #009dae2a;
    border-radius: 10px;

}

.contact form .title{
    font-size: 2em;
    text-align: center;
    margin-bottom: 30px;
}

.contact form div{
    display: flex;
    flex-direction: column;
    margin: 20px 0;
}

.contact form div label{
    font-weight: 700;
    margin-bottom: 20px;
}

.contact form div input{
    padding: 10px 30px;
    outline: none;
    border: 2px solid rgba(180, 180, 180, 0.288);
    border-radius: 10px;
    box-shadow: 0px 3px 2px rgba(0, 0, 0, .2);
}

.contact form input.send{
    margin: auto;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
    padding: 10px;
    background-color: #FF4F6E;
    color: #fff;
    width: 40%;
    outline: none;
    border: none;
    border-radius: 20px;
    margin-top: 20px;
}






/* breakpoints */

/* Mobile phones */
@media only screen and (max-width: 600px) {
    body {
        font-size: 10px;
    }
    .projects{
        padding: 0 30px;
    }
    .banner, .about{
        flex-direction: column-reverse;
        text-align: center;
        justify-content: center;
        align-items: center;
    }
    #navbar {
        justify-content: center;
        align-items: center;
        display: flex;
    }
    .services{
        flex-direction: column;
        margin: 50px 0;
        margin-bottom: 0px;
    }
    .services .left{
        width: 100%;
        margin-bottom: 30px;
    }
    .services .right{
        flex-direction: column;
    }

    .services .right .service{
        margin: 15px 0;
    }

    .services .right .service p{
        font-weight: 700;
        font-size: 2em;
    }
    .about{
        padding: 40px 20px;
    }
    .about .content{
        flex-direction: column;
    }
    .about .content .left img{
        scale: .8;
    }
    .about .header{
        margin: 0;
    }
    .about .content .right .top{
        flex-direction: column;
        margin-bottom: 20px;
    }
    .about .content .right .top .service{
        width: 300px;
        margin: 10px 0;
    }
    .about .content .right .top .service p{
        font-size: 1.4em;
    }
    .about .content .right .desc{
        z-index: 20;
    }
    .about .content .right .desc p{
        font-size: 1.5em;
        z-index: 15;
    }
    .projects .bottom{
        flex-direction: column;
        align-items: center;
    }
    .projects .bottom .service {
        margin: 0;
    }
    .projects .bottom .service .buttons{
        flex-direction: row;
        justify-content: space-between;
    }
    .projects .bottom .service .buttons button {
        padding: 10px 15px;
        max-width: 60%;
    }
    .projects .bottom .service a.exp{
        width: 60%;
        text-align: center;
        justify-content: center;
        font-size: 1.3em;
    }
    
    .projects .bottom div{
        margin: 20px 0;
    }

    .projects img.absolute{
        left: -60%;
        top: -40%;
        z-index: 1;
    }
    .projects{
        margin-top: 40px;
        padding-bottom: 0;
    }
    .projects .header{
        margin-bottom: 20px;
    }
    .skills.projects{
        margin-top: 40px;
    }
    .skills.projects .header{
        margin-bottom: 20px;
    }

    .skills.projects .bottom ul li{
        flex-direction: column;
    }
    .skills.projects .bottom ul li img{
        scale: .6;
    }
    .skills.projects .bottom ul li p{
        font-size: 1.9em;
        margin-bottom: 7px;
    }
    .contact form{
        width: 80%;
        padding: 20px 40px;
    }
    .contact form div label{
        font-size: 1.6em;
        margin-bottom: 5px;
    }
    .services .left p.title{
        font-size: 2.5em;
    }
    .about .header p.title, .projects .header p.title{
        font-size: 2.5em;
    }
}
  
/* Tablets */
@media only screen and (min-width: 601px) and (max-width: 1024px) {
    body {
        font-size: 16px;
      }
}

  
/* Laptops */
@media only screen and (min-width: 1025px) and (max-width: 1440px) {
    body {
        font-size: 18px;
      }
}
  
/* Desktops */
@media only screen and (min-width: 1441px) {
}


